<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="名称">
                <el-input style="width: 500px" v-model="form.name" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            label="启用"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不启用"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="创建时间">
            <el-date-picker
            v-model="form.create_time"
            type="date"
            placeholder="选择日期">
            </el-date-picker>
            </el-form-item>
            <el-form-item label="更新时间">
            <el-date-picker
            v-model="form.update_time"
            type="date"
            placeholder="选择日期">
            </el-date-picker>
            </el-form-item>

            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/column'

    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    id: null,
                    name: null,
                    description: null,
                    // picture: null,
                    price: null,
                    recommend: null,
                    status: null,
                    address: null,
                    rich_text: null,
                    category_id: null,
                },
                img: null,
                isNoEdit: true,
                isLoading: false,
                options: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                if (this.$route.query.id !== undefined) {
                    console.log(this.$route.query.id);
                    this.isNoEdit = true;
                    this.loadData()
                } else {
                    this.isNoEdit = false;
                }
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.name)) {
                    this.$message.error('请输入名称');
                    return;
                }

                if (!api.isStrValid(this.form.status)) {
                    this.$message.error('请选择状态');
                    return;
                }

                if (!api.isStrValid(this.form.create_time)) {
                    this.$message.error('请选择创建时间');
                    return;
                }

                if (!api.isStrValid(this.form.update_time)) {
                    this.$message.error('请选择更新时间');
                    return;
                }

                let data = api.objToFormData(this.form);
                this.isLoading = true;
                api.save(data, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'column'});
                    this.$route.meta.keepAlive = false;
                })
            },
            back() {
                this.$router.back();
            },

        }
    }
</script>
